<template>
  <div class="rejection-model">
    <b-modal id="modal-center" centered v-model="toggleModel">
      <template #modal-header>
        <h3>Rejection Reasons</h3>
      </template>

      <div class="content">
        <ChipSelector
          :isRadio="false"
          v-if="rejectionReasons"
          :options="
            rejectionReasons.map((reason) => {
              return { value: reason.id, label: reason.name };
            })
          "
          @update-selection="updateRejectionReasons"></ChipSelector>
      </div>
      <template #modal-footer>
        <div class="actions">
          <b-button @click="$emit('closeRejectionModel')"> Close </b-button>
          <b-button variant="danger" @click="$emit('rejectOffer')">
            Reject
          </b-button>
        </div>
      </template>
    </b-modal>
  </div>
</template>

<script>
import ChipSelector from '@/components/shared/form/chipSelector';
export default {
  props: {
    toggle: {
      type: Boolean,
      default: false,
    },
  },
  components: {
    ChipSelector,
  },
  data() {
    return {
      rejectionReasons: [],
      reasons: [],
    };
  },
  computed: {
    toggleModel: {
      get: function () {
        return this.toggle;
      },
      set: function () {
        this.$emit('closeRejectionModel');
      },
    },
  },
  methods: {
    updateRejectionReasons(values) {
      this.reasons = values;
    },
  },
  async mounted() {
    const { Data } = await this.$axios.$get('/user/rejectionReasons');
    this.rejectionReasons = Data;
  },
};
</script>

<style lang="scss">
header {
  &#modal-center___BV_modal_header_ {
    &.modal-header {
      justify-content: center;
      font-weight: bold;
      padding: 32px 16px;

      h3 {
        color: black;
        font-size: 20px;
      }
    }
  }
}
div {
  &#modal-center___BV_modal_content_ {
    &.modal-content {
      border-radius: 12px;
    }
  }
}

footer {
  &#modal-center___BV_modal_footer_ {
    &.modal-footer {
      .actions {
        display: flex;
        width: 100%;

        .btn {
          padding-top: 12px;
          padding-bottom: 12px;
          border-radius: 12px;
          font-weight: 600;
          border: none;
        }

        .btn-secondary {
          background-color: #f7f9fa;
          color: #111111;
          flex: 1;
          margin-right: 14px;
        }

        .btn-danger {
          background-color: #ffeeee;
          color: #ff5252;
          flex: 3;
        }
      }
    }
  }
}
</style>
