<template>
  <b-overlay :show="loading" rounded="sm" :opacity="1">
    <div class="offer-details">
      <div class="heading">
        <!-- Request Details -->
        <div class="container">
          <p>{{ offerDetails.name }}</p>
          <span class="light-gray" v-if="requestDetails.user">
            @ {{ requestDetails.user.full_name }}
          </span>
        </div>
      </div>
      <div :class="`status-bar ${offerStatus}`">
        <div class="container">{{ statusMessage }}</div>
      </div>
      <div
        class="rounds"
        v-if="offerDetails.activity_history"
        :class="offerDetails.activity_history.length > 1 && 'mobile-scroll'">
        <div class="round-card">
          <div class="text">
            <h3>Original Request</h3>
            <span>{{ requestDetails.created_at }}</span>
          </div>
          <hr />
          <div class="text">
            <h3>{{ requestDetails.budget }}</h3>
            <span>
              {{
                requestDetails.term === 1
                  ? $t('common.per_month')
                  : $t('common.per_night')
              }}
            </span>
          </div>
          <hr />
          <div class="text">
            <h3>{{ requestDetails.duration }}</h3>
            <span v-if="requestDetails.move_in_date">
              From,
              {{
                $dateFns.format(
                  new Date(requestDetails.move_in_date),
                  'dd MMM yy'
                )
              }}
            </span>
          </div>
        </div>
        <div
          class="round-card"
          :class="index === sortedRounds.length - 1 && offerStatus"
          v-for="(round, index) in sortedRounds"
          :key="index">
          <div class="text">
            <h3>{{ `Round ${index + 1}` }}</h3>
            <span>{{ round.created_at }}</span>
          </div>
          <hr />
          <div class="text">
            <h3>{{ round.price }}</h3>
            <span>
              {{
                requestDetails.term === 1
                  ? $t('common.per_month')
                  : $t('common.per_night')
              }}
            </span>
          </div>
          <hr />
          <div class="text">
            <h3>{{ round.duration }}</h3>
            <span>
              From,
              {{ $dateFns.format(new Date(round.move_in_date), 'dd MMM yy') }}
            </span>
          </div>
        </div>
      </div>
      <!-- Request Details -->
      <!-- Request Terms -->
      <div class="section">
        <div class="container">
          <p class="font-size-semi-large font-weight-bold">
            {{ $t('marketplace.request_details_title') }}
          </p>
          <p class="font-size-normal font-weight-bold">
            {{ $t(`marketplace.terms`) }}
          </p>
          <span
            class="chip"
            v-for="term in requestDetails.conditions"
            :key="term.id">
            {{ term.name }}
          </span>
        </div>
      </div>
      <!-- Request Rooms -->
      <div class="section">
        <div class="container">
          <div class="text">
            <p class="font-size-normal font-weight-bold mb-0">
              {{ $t('form.noOfRooms') }}
            </p>
            <div class="number-counter">
              <p>
                {{ $t('form.bedrooms') }}
              </p>
              <div class="number">
                <span>{{ requestDetails.num_of_bed_rooms || 0 }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- Offer Actions-->
      <div class="actions" v-if="offerStatus && offerStatus !== 'rejected'">
        <div
          class="reject-accept"
          v-if="
            offerStatus !== 'pending' &&
            offerStatus !== 'rejected' &&
            offerStatus !== 'accepted'
          ">
          <b-button variant="danger" @click="rejectionModel = !rejectionModel">
            {{ $t('offer.reject') }}
          </b-button>
          <b-button variant="primary" @click="updateOffer('accept')">
            {{ $t('offer.accept') }}
          </b-button>
        </div>
        <div class="chat_whatsapp d-flex" v-if="offerStatus === 'accepted'">
          <b-button
            variant="primary"
            class="chat-btn"
            @click="() => $router.push(localePath(`/dashboard/messaging`))">
            <img width="22px" height="22px" src="@/assets/images/icons/chat.svg" alt="Whatsapp">
            {{ $t('common.startChat') }}
          </b-button>
          <b-button
            class="whatsapp bg-transparent d-flex justify-content-center align-items-center"
            target="_blank"
            :href="`https://wa.me/${offerDetails.tenant_data.mobile}?text=I'm Landlord!`">
            <img width="24px" height="24px" src="@/assets/images/icons/whatsapp.svg" alt="Whatsapp">
            Whatsapp
          </b-button>
        </div>
      </div>
      <RejectionReasonsModel
        :toggle="rejectionModel"
        @closeRejectionModel="rejectionModel = false"
        @rejectOffer="updateOffer('reject')" />
    </div>
  </b-overlay>
</template>

<script>
import RejectionReasonsModel from '@/components/dashboard/RejectionReasonsModel';
export default {
  name: 'OfferDetailsPage',
  components: {
    RejectionReasonsModel,
  },
  data() {
    return {
      offerDetails: {},
      requestDetails: {},
      rejectionModel: false,
      loading: false,
      reqOwner: '',
    };
  },
  computed: {
    sortedRounds() {
      const sorted = this.offerDetails.activity_history
        .slice()
        .sort((a, b) => a.id - b.id);
      return sorted;
    },
    offerStatus() {
      if (this.offerDetails.status_details)
        return this.offerDetails.status_details.status_landloard;
      return '';
    },
    statusMessage() {
      if (this.offerStatus === 'accepted')
        return 'Congratulation your offer is accepted. You can Chat Now';
      else if (this.offerStatus === 'rejected') return 'Offer rejected';
      else if (this.offerStatus === 'pending')
        return `Awaiting ${
          this.requestDetails.user && this.requestDetails.user.full_name
        } Response`;
      else if (this.offerStatus === 'negotiating')
        return `${
          this.requestDetails.user && this.requestDetails.user.full_name
        } waiting for your response`;
      return '';
    },
  },
  methods: {
    async updateOffer(type) {
      try {
        const res = await this.$axios.$post(
          `/user/offers/${this.offerDetails.id}/interaction`,
          {
            type,
          }
        );
        
        this.$bvToast.toast(`Responded to offer successfully`, {
          autoHideDelay: 3000,
          appendToast: false,
          variant: 'success',
        });

        this.rejectionModel = false;

        setTimeout(() => {
          this.getOfferDetails();
        }, 1500);
      } catch (error) {
        const message = error.response.data.Data
          ? error.response.data.Data.message
          : error.response.data.Message;

        this.$bvToast.toast(`Error occurred ${message}`, {
          autoHideDelay: 3000,
          appendToast: false,
          variant: 'danger',
        });
      }
    },
    async getOfferDetails() {
      this.loading = true;
      const { Data } = await this.$axios.$get(
        `/user/getSingleOffer?id=${this.$route.params.offerId}`
      );
      this.offerDetails = Data;
      this.requestDetails = this.offerDetails.request;
      this.loading = false;
    },
  },
  async created() {
    await this.getOfferDetails();
  },
  beforeDestroy() {
    this.rejectionModel = false;
  },
};
</script>

<style lang="scss" scoped>
.offer-details {
  position: relative;
  min-height: 100vh;
  background-color: #f7f9fa;
  padding-bottom: 96px;

  .heading {
    background-color: white;
    padding: 32px 0;
    font-weight: bold;

    p {
      font-size: 18px;
      margin: 0;
    }
  }

  .status-bar {
    padding: 16px 0;
    font-weight: bold;
    background-color: #fff9ea;
    color: #ffc025;

    .container {
      text-align: center;
    }

    &.accepted {
      background-color: #def2e8;
      color: #00af48;
    }

    &.rejected {
      background-color: #ffeeee;
      color: #ff5252;
    }
  }

  .rounds {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 24px 0;
    background-color: white;
    margin-top: 16px;
    overflow: hidden;

    .round-card {
      padding: 24px 16px;
      border: 1px solid #e3e5e5;
      border-radius: 24px;
      width: 164px;
      min-width: 164px;
      margin: 0 16px;

      .text {
        color: #48535b;
        text-align: center;
        h3 {
          font-weight: bold;
          text-align: center;
        }
      }

      hr {
        margin: 16px 0;
      }

      &.accepted {
        border-color: #00af48;
      }

      &.rejected {
        border-color: #ff5252;
      }

      &.pending,
      &.negotiating {
        border-color: #ffc025;
      }
    }
  }

  .section {
    background-color: white;
    padding: 32px 0;
    margin-top: 18px;

    &.address {
      .container {
        display: flex;
        align-items: flex-start;

        .svg-icon {
          margin: 2px 16px 0 0;
        }
      }
    }

    .number-counter {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 16px 0;
      border-bottom: 1px solid #e3e5e5;

      &:last-child {
        border: none;
      }

      p {
        margin: 0;
        font-weight: 600;
        font-size: 16px;
      }

      .number {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 40px;
        border: 1px solid #e3e5e5;
        border-radius: 50%;
      }
    }

    .chip {
      display: inline-block;
      padding: 8px 12px;
      background-color: #e6f2ff;
      color: #0078ff;
      font-weight: bold;
      border-radius: 32px;
      margin: 0px 12px 12px 0;

      &:last-child {
        margin: 0;
      }
    }
  }

  .actions {
    padding: 24px 16px 16px;
    background-color: white;
    position: fixed;
    bottom: 70px;
    left: 0;
    width: 100%;
    @media(min-width: 768px){
      bottom: 0 !important;
    }

    .btn {
      padding-top: 12px;
      padding-bottom: 12px;
      border-radius: 12px;
      font-weight: 600;
      border: none;
    }

    .reject-accept {
      display: flex;

      .btn-danger {
        background-color: #ffeeee;
        color: #ff5252;
        flex: 1;
        margin-right: 14px;
      }

      .btn-primary {
        background-color: #0078ff;
        flex: 3;
      }
    }

    .chat_whatsapp {
      gap: 10px;
      .chat-btn, .whatsapp {
        width: 100%;
        gap: 5px;
      }
  
      .whatsapp {
        color: #07B925;
        border: 1px solid #07B925 !important;
      }
    }

    .negotiate-btn {
      width: 100%;
      background-color: #e6f2ff;
      color: #0078ff;
      margin-top: 14px;
    }
  }
}

@media screen and (max-width: 560px) {
  .offer-details {
    .rounds {
      &.mobile-scroll {
        justify-content: flex-start;
        overflow-x: scroll;
      }
    }
  }
}
</style>
